<!-- eslint-disable max-len -->
<template>
  <!-- 上 -->
  <section class="row">
    <div class="card border-dark paperDarkBg">
      <div class="card-body d-flex align-items-start text-uppercase">
        <span class="h4 me-3">step 1</span
        ><span class="h3" style="font-family: BellGothicStd">Your Oder</span>
      </div>
    </div>
    <div class="col-md-4 border border-dark">
      <div class="list-group" id="list-tab" role="tablist">
        <template v-for="(item, index) in orderList" :key="index">
          <a
            href="#"
            @click.prevent="getOrder(item.orderId)"
            class="fw-bold my-2 d-flex justify-content-between align-items-center text-decoration-none"
          >
            <span class="position-relative"
              >{{ item.cookieId }}
              <CircleThree
                :class="{ 'd-none': tempData._id !== item.orderId }" /></span
            ><span class="badge text-bg-primary rounded-pill">{{
              item.num
            }}</span>
          </a>
        </template>
      </div>
    </div>
    <div class="col-md-8 border border-dark">
      <Orderinfo :tempData="tempData" />
    </div>
  </section>
  <!-- 下 -->
  <section class="row">
    <div class="card border-dark paperDarkBg">
      <div class="card-body d-flex align-items-start text-uppercase">
        <span class="h4 me-3">step 2</span
        ><span class="h3" style="font-family: BellGothicStd"
          >Thank you for coming</span
        >
      </div>
    </div>
    <div class="card border-dark">
      <div class="d-flex justify-content-center">
        <img src="@/assets/images/thanks.jpg" alt="" class="w-sm-50" />
      </div>
    </div>
  </section>
</template>

<style lang="scss" scoped>
.circle-3 {
  width: 10rem;
}
</style>

<script>
import Cookies from 'js-cookie';
import CircleThree from '@/components/helpers/focus/CircleThree.vue';
import Orderinfo from '@/components/order/OrderInfo.vue';
import { apiGetOrder } from '@/assets/javascript/api';

export default {
  data() {
    return {
      orderList: [],
      tempData: {},
    };
  },
  components: {
    CircleThree,
    Orderinfo,
  },
  methods: {
    getOrder(id) {
      apiGetOrder(id)
        .then((res) => {
          this.tempData = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getCookie() {
      if (Cookies.get('luluBakeOrder')) {
        this.orderList = [];
        const tempCookie = Cookies.get('luluBakeOrder').split(';');
        Object.values(tempCookie).forEach((item) => {
          this.orderList.push(JSON.parse(item));
        });
        this.orderList.reverse();
        this.getOrder(this.orderList[0].orderId);
      }
    },
  },
  mounted() {
    this.getCookie();
  },
};
</script>
